.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgba(71,71,71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
    max-width: 500px;
}

.image{
    border-radius: 20%;
    width: 150px;
    height: 150px;
}

.heading{
    margin: auto 8px;
}